import emptyTips from '@/components/emptyTips';
export default {
  props: {
    userType: {
      Number,
      default: 0
    }
  },
  data() {
    return {
      features:[],
      showTips: 0
    }
  },
  created() {
    // 获取首页功能列表
    this.$http.get(`${WX_URL}base/function/getAreaItems/${this.userType}`).then((response) => {
      response = response.data;
      if (response.success) {
        this.features = response.data;
        if (!this.features.length) {
          this.showTips = 1;
        } else {
          this.showTips = 0;
        }
      }
    });
  },
  methods: {
    // 选择进入功能列表
    chooseFeature(val) {
      this.$router.push(val.url);
    },
    // 查看更多功能列表
    chooseMoreFeature() {
      console.log("111");
    }
  },
  components: {
    emptyTips
  }
}




